/*Popup*/
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  opacity: 1;
  visibility: hidden;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 30px 3px;
}

.popup:target {
  opacity: 1;
  visibility: visible;
}

.popup_body {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 10px;
  margin: 5px;
}

.popup_close {
  position: absolute;
  right: 10px;
  top: 10px;
  color: #000;
  text-decoration: none;
}

.popup_close:hover {
  text-decoration: none;

}

.popup_content {
  background-color: black;
  color: #000;
  max-width: 800px;
  padding: 80px;
  position: relative;
  margin: 1px;
  text-align: center;
  border-radius: 5%;
}

@import url('https://fonts.googleapis.com/css?family=Mitr');

.popup_title {
  color: #fff;
  font-size: 35px;
  font-family: Mitr, sans-serif;
  text-align: center;
  margin: 5px;
  margin-bottom: 20px;
  line-height: 50px;
  text-transform: uppercase;
}

.popup p {
  font-size: 15px;
  letter-spacing: 1px;
  margin-top: 15px;
}

.popup_text {
  margin-top: 5px;
  margin-bottom: 10px;
  font-size: 16px;
}

.donate-pay {
  display: inline-block;
  cursor: pointer;
  color: #ffffff !important;
  text-decoration: none;
  font-family: Arial, sans-serif;
  font-size: 15px;
  font-weight: bold;
  padding: 13px 60px;
  text-align: center;
  margin-top: 20px;
  background: linear-gradient(266.33deg, #B533F2 14.11%, #AB0EC5 53.08%, #540062 88.53%);
  border-radius: 40px;

}

.donate-pay:hover {
  box-shadow: inset 0 0 18px 6px rgba(255, 255, 255, 0.25);
  text-decoration: none;
  color: #fff;
}

.donate-pay:active {
  position: relative;
  top: 1px;
  text-decoration: none;
}


.preloader {
  /*фиксированное позиционирование*/
  position: fixed;
  /* координаты положения */
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  /* фоновый цвет элемента */
  background: #e0e0e0;
  /* размещаем блок над всеми элементами на странице (это значение должно быть больше, чем у любого другого позиционированного элемента на странице) */
  z-index: 1001;
}

.preloader__row {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: #ff0037;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes preloader-bounce {

  0%,
  100% {
    transform: scale(0);
  }

  50% {
    transform: scale(1);
  }
}

.loaded_hiding .preloader {
  transition: 0.3s opacity;
  opacity: 0;
}

.loaded .preloader {
  display: none;
}

