#news {
  padding-bottom: 10rem;
  padding-top: 10rem;
  text-align: center;
  align-items: center;
  justify-content: center;

  h1 {
    font-family: "merriweather-bold", serif;
    font-size: 4.8rem;
    color: #252525;
    line-height: 1.25;
    margin-bottom: 1.2rem;
  }

  .cards {
    margin-top: 8%;
    display: grid;
    grid-template-columns: 32% 32% 32.5%;
    gap: 20px;
  }

  .news-card {
    box-shadow: 0 0 16px -5px #000000;
    margin-top: 12%;
    padding-bottom: 10%;
    border-radius: 20px;

    h5 {
      margin-top: 20px;
      letter-spacing: 0;
      font-size: 20px;
      font-family: 'Inter', sans-serif;
      text-transform: initial;
    }

    h6 {
      font-size: 15px;
      letter-spacing: 0;
      font-family: 'Inter', sans-serif;
    }

    img {
      align-items: center;
      margin-top: 20%;
      width: 70%;
    }

    .read {
      background: linear-gradient(to bottom, #ff5bb0 5%, #ef027d 100%);
      border-radius: 11px;
      display: inline-block;
      cursor: pointer;
      color: #ffffff;
      font-family: Arial, sans-serif;
      font-size: 15px;
      font-weight: bold;
      padding: 13px 50px;
      text-decoration: none;
      text-shadow: 0px 1px 0px #c70067;
      transition: 0.5s;
      margin-top: 50px;

      &:hover {
        transition: 0.5s;
        background: #ef027d;
      }
    }

    .read:active {
      position: relative;
      top: 1px;
    }
  }
}

.loader {
  position: fixed;
  top: calc(50% - 40px);
  left: calc(50% - 40px);
  border: 10px solid #f3f3f3;
  border-top: 10px solid #ef027d;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 2s linear infinite;
  transition: 2s;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 1080px) {
  #news .cards {
    display: inline-flex;
  }
}

@media only screen and (max-width: 800px) {
  #news .cards {
    display: inline-block;
    .news-card {
      margin-left: 15%;
      width: 70%;
    }
  }
}

